export async function getInvoiceByUserID(id: any) {
  try {
    const res = await fetch(`https://localhost:7257/api/Invoice/${id}`);

    if (!res.ok) {
      throw new Error(`HTTP error! Status: ${res.status}`);
    }

    // Ki·ªÉm tra n·∫øu kh√¥ng c√≥ n·ªôi dung tr·∫£ v·ªÅ
    const text = await res.text();
    if (!text) {
      console.warn("API tr·∫£ v·ªÅ r·ªóng");
      return null;
    }

    const data = JSON.parse(text);
    return data;
  } catch (error) {
    console.error("L·ªói khi l·∫•y h√≥a ƒë∆°n theo ID:", error);
    return null;
  }
}
